import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import { useQueryParam, StringParam } from 'use-query-params';

const WidgetContainer = styled.div`
  display: block;
  flex: 1;
  justify-content: center;
  align-items: center;
  outline: none;
  min-height: 675px;
  z-index: -1;
`;

const MoonpayWidgetIFrame = styled.iframe`
  height: 100%;
  width: 100%;
  min-height: 425px;
`;

const MoonpayWidget = () => {
  const [isInit, setIsInit] = useState(false);
  const [currencyCode, setCurrencyCode] = useQueryParam("currencyCode", StringParam);
  const [walletAddress, setWalletAddress] = useQueryParam("walletAddress", StringParam);
  console.debug('[MoonpayWidget]: search', currencyCode, walletAddress)
  let moonpaySrcAddress = 'https://buy-staging.moonpay.io?apiKey=pk_test_3aM6rCKM7VxglAXwpo6xEm031iQWBbZq&colorCode=%23163150';

  if (currencyCode && walletAddress) {
    console.log('[MoonpayWidget]: 3')
    moonpaySrcAddress += '&walletAddress=' + walletAddress;
    moonpaySrcAddress += '&currencyCode=' + currencyCode;
  }

  useEffect(() => {
    setIsInit(true);
  }, [])

  console.debug('[MoonpayWidget]: moonpaySrcAddress', moonpaySrcAddress)
  return (
    <WidgetContainer role="group">
      {isInit && (<MoonpayWidgetIFrame
        allow="accelerometer; autoplay; camera; gyroscope; payment"
        frameBorder={0}
        src={moonpaySrcAddress}
      >
        <p>Your browser does not support iframes.</p>
      </MoonpayWidgetIFrame>)}
      {`search: ${currencyCode} - ${walletAddress}`}
      <br />
      {`moonpaySrcAddress: ${moonpaySrcAddress}`}
    </WidgetContainer>
  );
};

export default MoonpayWidget;
